$card-day-width: 100px;
$padding-side: 10px;
$gap-width: 1px;
$days-in-week: 7;
$block-day-size: $card-day-width + $gap-width;
$calendar-width: $card-day-width * $days-in-week + $gap-width * ($days-in-week - 1) + $padding-side * 2;

$days-of-week:
'mon' 0,
'tue' 1,
'wed' 2,
'thu' 3,
'fri' 4,
'sat' 5,
'sun' 6;

